'use client';

import { useState } from 'react';
import { FiSend, FiPlus, FiTrash2 } from 'react-icons/fi';
import dynamic from 'next/dynamic';

// Dynamically import JSON editor to avoid SSR issues
const JsonEditor = dynamic(() => import('react-json-editor-ajrm'), { ssr: false });

interface KeyValuePair {
  key: string;
  value: string;
}

export default function OpenApiTest() {
  const [method, setMethod] = useState('GET');
  const [url, setUrl] = useState('');
  const [queryParams, setQueryParams] = useState<KeyValuePair[]>([{ key: '', value: '' }]);
  const [headers, setHeaders] = useState<KeyValuePair[]>([{ key: '', value: '' }]);
  const [requestBody, setRequestBody] = useState<any>({});
  const [response, setResponse] = useState<any>(null);
  const [loading, setLoading] = useState(false);

  const handleAddParam = (type: 'query' | 'header') => {
    if (type === 'query') {
      setQueryParams([...queryParams, { key: '', value: '' }]);
    } else {
      setHeaders([...headers, { key: '', value: '' }]);
    }
  };

  const handleRemoveParam = (type: 'query' | 'header', index: number) => {
    if (type === 'query') {
      setQueryParams(queryParams.filter((_, i) => i !== index));
    } else {
      setHeaders(headers.filter((_, i) => i !== index));
    }
  };

  const handleParamChange = (
    type: 'query' | 'header',
    index: number,
    field: 'key' | 'value',
    value: string
  ) => {
    if (type === 'query') {
      const newParams = [...queryParams];
      newParams[index][field] = value;
      setQueryParams(newParams);
    } else {
      const newHeaders = [...headers];
      newHeaders[index][field] = value;
      setHeaders(newHeaders);
    }
  };

  const handleSend = async () => {
    setLoading(true);
    try {
      // Convert query params and headers to objects
      const queryParamsObj = queryParams.reduce((acc, { key, value }) => {
        if (key) acc[key] = value;
        return acc;
      }, {} as Record<string, string>);

      const headersObj = headers.reduce((acc, { key, value }) => {
        if (key) acc[key] = value;
        return acc;
      }, {} as Record<string, string>);

      const response = await fetch('/api/openapi', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          method,
          url,
          queryParams: queryParamsObj,
          headers: headersObj,
          body: method !== 'GET' ? requestBody : undefined
        }),
      });

      const data = await response.json();
      setResponse(data);
    } catch (error) {
      setResponse({
        error: 'Failed to make request',
        details: error instanceof Error ? error.message : 'Unknown error'
      });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="max-w-6xl mx-auto p-6">
      <div className="bg-white rounded-lg shadow-sm border p-6">
        {/* Request Section */}
        <div className="space-y-4">
          {/* Method and URL */}
          <div className="flex gap-2">
            <select
              value={method}
              onChange={(e) => setMethod(e.target.value)}
              className="px-3 py-2 border rounded-md bg-gray-50 text-sm"
            >
              <option>GET</option>
              <option>POST</option>
              <option>PUT</option>
              <option>PATCH</option>
              <option>DELETE</option>
            </select>
            <input
              type="text"
              value={url}
              onChange={(e) => setUrl(e.target.value)}
              placeholder="Enter request URL"
              className="flex-1 px-3 py-2 border rounded-md text-sm"
            />
            <button
              onClick={handleSend}
              disabled={loading}
              className="px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600 disabled:opacity-50 flex items-center gap-2"
            >
              <FiSend className={loading ? 'animate-pulse' : ''} />
              {loading ? 'Sending...' : 'Send'}
            </button>
          </div>

          {/* Query Params */}
          <div className="space-y-2">
            <div className="flex justify-between items-center">
              <h3 className="text-sm font-medium">Query Params</h3>
              <button
                onClick={() => handleAddParam('query')}
                className="text-blue-500 hover:text-blue-600"
              >
                <FiPlus />
              </button>
            </div>
            {queryParams.map((param, index) => (
              <div key={index} className="flex gap-2">
                <input
                  type="text"
                  value={param.key}
                  onChange={(e) => handleParamChange('query', index, 'key', e.target.value)}
                  placeholder="Key"
                  className="flex-1 px-3 py-2 border rounded-md text-sm"
                />
                <input
                  type="text"
                  value={param.value}
                  onChange={(e) => handleParamChange('query', index, 'value', e.target.value)}
                  placeholder="Value"
                  className="flex-1 px-3 py-2 border rounded-md text-sm"
                />
                <button
                  onClick={() => handleRemoveParam('query', index)}
                  className="text-red-500 hover:text-red-600"
                >
                  <FiTrash2 />
                </button>
              </div>
            ))}
          </div>

          {/* Headers */}
          <div className="space-y-2">
            <div className="flex justify-between items-center">
              <h3 className="text-sm font-medium">Headers</h3>
              <button
                onClick={() => handleAddParam('header')}
                className="text-blue-500 hover:text-blue-600"
              >
                <FiPlus />
              </button>
            </div>
            {headers.map((header, index) => (
              <div key={index} className="flex gap-2">
                <input
                  type="text"
                  value={header.key}
                  onChange={(e) => handleParamChange('header', index, 'key', e.target.value)}
                  placeholder="Key"
                  className="flex-1 px-3 py-2 border rounded-md text-sm"
                />
                <input
                  type="text"
                  value={header.value}
                  onChange={(e) => handleParamChange('header', index, 'value', e.target.value)}
                  placeholder="Value"
                  className="flex-1 px-3 py-2 border rounded-md text-sm"
                />
                <button
                  onClick={() => handleRemoveParam('header', index)}
                  className="text-red-500 hover:text-red-600"
                >
                  <FiTrash2 />
                </button>
              </div>
            ))}
          </div>

          {/* Request Body */}
          {method !== 'GET' && (
            <div className="space-y-2">
              <h3 className="text-sm font-medium">Request Body</h3>
              <div className="border rounded-md overflow-hidden">
                <JsonEditor
                  placeholder={requestBody}
                  onChange={(value: any) => setRequestBody(value.jsObject)}
                  theme="light_mitsuketa_tribute"
                  locale="en"
                  height="200px"
                  width="100%"
                />
              </div>
            </div>
          )}
        </div>

        {/* Response Section */}
        {response && (
          <div className="mt-8 space-y-2">
            <h3 className="text-sm font-medium">Response</h3>
            <div className="border rounded-md overflow-hidden">
              <JsonEditor
                placeholder={response}
                viewOnly={true}
                theme="light_mitsuketa_tribute"
                locale="en"
                height="200px"
                width="100%"
              />
            </div>
          </div>
        )}
      </div>
    </div>
  );
} 